













































	import header from "../../../components/header.vue"
	export default { 
		components:{
			headers:header
		},
		data(){
			return {
				imgSrc:"../../../static/img/back.png",
				title:"账号登陆",
				user:"",
				pass:""
				// imgSrc:imgs
			}
		},
		methods:{
			back(){
				uni.navigateBack();
			},
			duanxin(){
				uni.navigateTo({
				    url: '/pages/page/duanxin/duanxin'
				});
			},
			goInfo(){
				uni.navigateTo({
				    url: '/pages/page/info/info'
				});
			},
			saoma(){
				uni.getLocation({
					altitude:true,
					type: 'wgs84', //返回可以用于uni.openLocation的经纬度
					success: function (res) {
						const latitude = res.latitude;
						const longitude = res.longitude;
						uni.openLocation({
							latitude: latitude,
							longitude: longitude,
							name:"曹杨花苑",
							address:"芝川路曹阳花苑",
							success: function () {
								console.log('success');
							}
						});
					}
				});
				
			},
			isLogin(){
				 if (this.user.length < 6) {
						 uni.showToast({
							icon:"none",
							title: '用户名必须大于6个字符',
							duration: 2000
						});
						return false;
                     } else if (!this.vali.is_phone(this.user)) {
                        uni.showToast({
							icon:"none",
                        	title: '手机号码格式不正确且只能输入手机号',
                        	duration: 2000
                        });
						 return false;
                     }

                     if (this.pass.length < 6) {
                         uni.showToast({
							 icon:"none",
                         	title: '密码长度大于6',
                         	duration: 2000
                         });
                         return false;
                     } else if (!this.vali.is_ZmOrNum(this.pass)) {
                          uni.showToast({
							  icon:"none",
                         	title: '密码格式不正确',
                         	duration: 2000
                         });
                         return false;
                     }
					 this.login();
			},
			login(){
				uni.request({
					url: this.userUrl + "/BtCApi/Login/APP_Login", //仅为示例，并非真实接口地址。
					method:'POST',
					 header: {
						'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
					},
					data: {
						Mob: this.user,
						LoginPass: this.pass,
						DeviceId: '856d0cdb-fee1-4e04-b29c-987b8299453e',
						PhoneVersion: null,
						ClientVersion: '',
						ClientType: '0',
					},
					
					success: (res) => {
						if(res.data.status == true){
							console.log(res.data.data.Signid)
							uni.showToast({
								icon:"none",
								title: "登陆成功",
								duration: 2000
							});
							uni.setStorageSync('UserID', res.data.data.ID);
							uni.setStorageSync('Signid', res.data.data.Signid);
							uni.setStorageSync('DeviceId', res.data.data.DeviceId);
							uni.setStorageSync('PhoneVersion', res.data.data.PhoneVersion);
							uni.setStorageSync('ClientVersion', res.data.data.ClientVersion);
							uni.setStorageSync('ClientType', res.data.data.ClientType);
							uni.switchTab({
								url: '/pages/router/mine/mine'
							});
						}else{
							uni.showToast({
								icon:"none",
								title: res.data.msg,
								duration: 2000
							});
						}
						
						//this.text = 'request success';
					}
				});
			}
		}
	}
